import React from 'react'
import { NavLink, Redirect, withRouter,Switch} from 'react-router-dom';
import './doc.css';
import RouteWithSubRoutes from './RouteWithSubRoutes';
import routes from '../router.config';

export const Core = () =>{
    return <h1>Core</h1>
}
export const Guide = () =>{
    return <h1>Guide</h1>
}
export const Api = () =>{
    return <h1>Api</h1>
}
export const Hooks = () =>{
    return <h1>Hooks</h1>
}

const Doc = ({routes}) => {
    return (
        <div className='doc'>
            <div className='content'>
            <Switch>
                {routes.map((route) => (
                    <RouteWithSubRoutes {...route} />
                ))}
                <Redirect from='/doc' to='/doc/core'/>
            </Switch>
            </div>
            <ol>
                <li><NavLink to='/doc/core' >核心概念</NavLink></li>
                <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                <li><NavLink to='/doc/api'  >API</NavLink></li>
                <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
            </ol>
        </div>
    )
}

export default withRouter(Doc);